// 持久化
const INFO_KEY = '16shevvy_info'
const HISTORY_KRY = 'history_data'

// 获取个人信息
export const getLocaUserInfo = () => {
  const defaultObj = { token: '', userId: '' }
  const result = localStorage.getItem(INFO_KEY)
  return result ? JSON.parse(result) : defaultObj
}

// 设置个人信息
export const setgetLocaUserInfo = (obj) => {
  localStorage.setItem(INFO_KEY, JSON.stringify(obj))
}

// 移除个人信息
export const removegetLocaUserInfo = () => {
  localStorage.removeItem(INFO_KEY)
}

// 获取历史搜索
export const getHistoryList = () => {
  const result = localStorage.getItem(HISTORY_KRY)
  return result ? JSON.parse(result) : []
}

// 设置历史搜索
export const setHistoryList = (arr) => {
  localStorage.setItem(HISTORY_KRY, JSON.stringify(arr))
}
