// 商品
import $axios from '@/utils/axios'

// 获取商品搜索列表接口
export const getProList = (paramsObj) => {
  const { sortType, categoryId, goodsName } = paramsObj
  return $axios.get('/goods/list', {
    params: {
      sortType,
      categoryId,
      goodsName,
      page: 0
    }
  })
}

// 获取商品详细接口
export const getProDetail = (goodsId) => $axios.get('/goods/detail', {
  params: {
    goodsId
  }
})

// 获取商品评价条数接口
export const getProComments = (goodsId, limit) => $axios.get('/comment/listRows', {
  params: {
    goodsId,
    limit
  }
})
