<script>
import { addressAdd } from '@/api/address'
import { areaList } from '@vant/area-data'
export default {
  data () {
    return {
      areaList
    }
  },
  methods: {
    async onSave (content) {
      console.log(content)
      const region = [
        {
          label: content.province
        },
        {
          label: content.city
        },
        {
          label: content.county
        }
      ]
      await addressAdd(content.name, content.tel, region)
    }
  },
  computed: {
    newareaList () {
      return 1
    }
  },
  async created () {
  }
}
</script>

<template>
  <div>
    <van-nav-bar title="添加收货地址" left-arrow @click-left="$router.go(-1)" fixed/>
    <div class="address-edit">
      <van-address-edit
      :area-list="areaList"
      show-search-result
      :area-columns-placeholder="['请选择', '请选择', '请选择']"
      @save="onSave"
      />
    </div>
  </div>
</template>

<style lang="less" scoped>
.address-edit{
  margin-top: 50px;
}
</style>
