<script>
import { getProDetail, getProComments } from '@/api/produce'
import { useAddCart } from '@/api/cart'
import { Dialog, Toast } from 'vant'
import { mapState } from 'vuex'
import defaultImg from '@/assets/default-avatar.png'
import CountBox from '@/components/CountBox.vue'
import loginConfirm from '@/mixins/loginConfirm.js'

export default {
  name: 'ProDetail',
  mixins: [loginConfirm],
  data () {
    return {
      detail: {}, // 商品详细数据
      images: [], // 轮播图数据
      commentList: [], // 商品评价数据
      total: 0, // 评论总条数
      defaultImg, // 默认头像
      mode: 'cart', // 模式
      show: false, // 控制弹层
      changeCount: 1, // 控制商品添加或购买数量
      skuId: '',
      isLoading: false,
      current: 0
    }
  },
  methods: {
    onChange (index) {
      this.current = index
    },

    // 添加购物车弹窗
    addFn (type) {
      this.mode = type
      this.show = true
    },
    // 立即购买弹窗
    buyFn (type) {
      this.mode = type
      this.show = true
    },

    // 添加购物车
    async addCart () {
      // 未登录
      if (this.loginConfirm()) return

      // 已登录
      // 调用加入购物车接口
      this.skuId = this.detail.skuList[0].goods_sku_id
      const res = await useAddCart(this.goodsId, this.changeCount, this.skuId)
      Toast('操作成功')
    },

    // 立即购买
    goBuyNow () {
      // 未登录
      if (this.loginConfirm()) return

      // 已登录
      // 跳转到订单页面
      this.$router.push({
        path: '/pay',
        query: {
          mode: 'buyNow',
          goodsId: this.goodsId,
          skuId: this.skuIdd,
          goodsNum: this.changeCount
        }
      })
    },

    // 下拉刷新
    onRefresh () {
      this.isLoading = true
      setTimeout(() => {
        location.reload()
        this.isLoading = false
      }, 900)
    }
  },
  computed: {
    goodsId () {
      return this.$route.params.id
    },
    skuIdd () {
      return this.detail.skuList[0].goods_sku_id
    },
    ...mapState('cart', ['cartList'])
  },
  async created () {
    // 获取商品详细数据
    const { data: { detail } } = await getProDetail(this.goodsId)
    this.detail = detail
    this.images = detail.goods_images

    // 获取商品评价条数
    const { data: { list, total } } = await getProComments(this.goodsId, 3)
    this.total = total
    this.commentList = list
    // console.log(list)
  },
  watch: {
    show () {
      if (this.show === false) { this.changeCount = 1 }
    }
  },
  components: {
    CountBox
  }
}
</script>

<template>
  <div class="prodetail">
    <van-nav-bar fixed title="商品详情页" left-arrow @click-left="$router.go(-1)" />

    <van-pull-refresh
    v-model="isLoading"
    success-text="刷新成功"
    @refresh="onRefresh"
    >
    <van-swipe :autoplay="3000" @change="onChange">
      <van-swipe-item v-for="(image, index) in images" :key="index">
        <img :src="image.external_url" />
      </van-swipe-item>

      <template #indicator>
        <div class="custom-indicator">{{ current + 1 }} / {{ images.length }}</div>
      </template>
    </van-swipe>

    <!-- 商品说明 -->
    <div class="info">
      <div class="title">
        <div class="price">
          <span class="now">￥{{ detail.goods_price_min }}</span>
          <span class="oldprice">￥{{ detail.goods_price_max}}</span>
        </div>
        <div class="sellcount">已售{{ detail.goods_sales }}件</div>
      </div>
      <div class="msg text-ellipsis-2">
        {{ detail.goods_name }}
      </div>

      <div class="service">
        <div class="left-words">
          <span><van-icon name="passed" />七天无理由退货</span>
          <span><van-icon name="passed" />48小时发货</span>
        </div>
        <div class="right-icon">
          <van-icon name="arrow" />
        </div>
      </div>
    </div>

    <!-- 商品评价 -->
    <div class="comment">
      <div class="comment-title">
        <div class="left">商品评价 ({{total}}条)</div>
        <div class="right">查看更多 <van-icon name="arrow" /> </div>
      </div>
      <div class="comment-list">
        <div class="comment-item" v-for="i in commentList" :key="i.comment_id">
          <div class="top">
            <img :src=" i.user.avatar_url  || defaultImg" alt="">
            <div class="name">{{ i.user.nick_name }}</div>
            <van-rate :size="16" :value="5" color="#ffd21e" void-icon="star" void-color="#eee"/>
          </div>
          <div class="content">
            {{ i.content }}
          </div>
          <div class="time">
            {{ i.create_time }}
          </div>
        </div>
      </div>
    </div>

    <!-- 商品描述 -->
    <div class="desc" v-html="detail.content">
    </div>

    <!-- 底部 -->
    <!-- <div class="footer">
      <div class="icon-home">
        <van-icon name="wap-home-o" />
        <span @click="$router.push('/')">首页</span>
      </div>
      <div class="icon-cart">
        <van-icon name="shopping-cart-o" />
        <span>购物车</span>
      </div>
      <div class="btn-add" @click="addFn('cart')">加入购物车</div>
      <div class="btn-buy" @click="buyFn('buyNow')">立刻购买</div>
    </div> -->

    <van-goods-action>
      <van-goods-action-icon @click="$router.push('/')" icon="wap-home-o" text="首页" />
      <van-goods-action-icon @click="$router.push('/cart')" icon="shopping-cart-o" text="购物车" :badge="cartList.length"  />
      <van-goods-action-button @click="addFn('cart')" color="#be99ff" type="warning" text="加入购物车" />
      <van-goods-action-button @click="buyFn('buyNow')" color="#7232dd" type="danger" text="立即购买" />
    </van-goods-action>

    <!-- 弹窗 -->
    <van-action-sheet v-model="show" :title="mode === 'cart' ? '加入购物车' : '立刻购买'">
      <div class="product">
        <div class="product-title">
          <div class="left">
            <img :src="detail.goods_image" alt="">
          </div>
          <div class="right">
            <div class="price">
              <span>¥</span>
              <span class="nowprice">{{ detail.goods_price_min }}</span>
            </div>
            <div class="count">
              <span>库存</span>
              <span>{{ detail.stock_total }}</span>
            </div>
          </div>
        </div>
        <div class="num-box">
          <span>数量</span>
          <CountBox v-model="changeCount"></CountBox>
        </div>
        <div class="showbtn" v-if="detail.stock_total !== 0">
          <div class="btn" @click="addCart" v-if="mode === 'cart'">加入购物车</div>
          <div @click="goBuyNow" class="btn now" v-else>立刻购买</div>
        </div>
        <div class="btn-none" v-else>该商品已抢完</div>
      </div>
    </van-action-sheet>
    </van-pull-refresh>
  </div>
</template>

<style lang="less" scoped>
.prodetail {
  padding-top: 46px;
  ::v-deep .van-icon-arrow-left {
    color: #333;
  }
  img {
    display: block;
    width: 100%;
  }
  .custom-indicator {
    position: absolute;
    right: 10px;
    bottom: 10px;
    padding: 5px 10px;
    font-size: 12px;
    background: rgba(0, 0, 0, 0.1);
    border-radius: 15px;
  }
  .desc {
    width: 100%;
    overflow: scroll;
    ::v-deep img {
      display: block;
      width: 100%!important;
    }
  }
  .info {
    padding: 10px;
  }
  .title {
    display: flex;
    justify-content: space-between;
    .now {
      color: #fa2209;
      font-size: 20px;
    }
    .oldprice {
      color: #959595;
      font-size: 16px;
      text-decoration: line-through;
      margin-left: 5px;
    }
    .sellcount {
      color: #959595;
      font-size: 16px;
      position: relative;
      top: 4px;
    }
  }
  .msg {
    font-size: 16px;
    line-height: 24px;
    margin-top: 5px;
  }
  .service {
    display: flex;
    justify-content: space-between;
    line-height: 40px;
    margin-top: 10px;
    font-size: 16px;
    background-color: #fafafa;
    .left-words {
      span {
        margin-right: 10px;
      }
      .van-icon {
        margin-right: 4px;
        color: #fa2209;
      }
    }
  }

  .comment {
    padding: 10px;
  }
  .comment-title {
    display: flex;
    justify-content: space-between;
    .right {
      color: #959595;
    }
  }

  .comment-item {
    font-size: 16px;
    line-height: 30px;
    .top {
      height: 30px;
      display: flex;
      align-items: center;
      margin-top: 20px;
      img {
        width: 20px;
        height: 20px;
      }
      .name {
        margin: 0 10px;
      }
    }
    .time {
      color: #999;
    }
  }

  .footer {
    position: fixed;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 55px;
    background-color: #fff;
    border-top: 1px solid #ccc;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    .icon-home, .icon-cart {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      font-size: 14px;
      .van-icon {
        font-size: 24px;
      }
    }
    .btn-add,
    .btn-buy {
      height: 36px;
      line-height: 36px;
      width: 120px;
      border-radius: 18px;
      background-color: #ffa900;
      text-align: center;
      color: #fff;
      font-size: 14px;
    }
    .btn-buy {
      background-color: #fe5630;
    }
  }
}

.tips {
  padding: 10px;
}
.product {
  .product-title {
    display: flex;
    .left {
      img {
        width: 90px;
        height: 90px;
      }
      margin: 10px;
    }
    .right {
      flex: 1;
      padding: 10px;
      .price {
        font-size: 14px;
        color: #fe560a;
        .nowprice {
          font-size: 24px;
          margin: 0 5px;
        }
      }
    }
  }

  .num-box {
    display: flex;
    justify-content: space-between;
    padding: 10px;
    align-items: center;
  }

  .btn, .btn-none {
    height: 40px;
    line-height: 40px;
    margin: 20px;
    border-radius: 20px;
    text-align: center;
    color: rgb(255, 255, 255);
    background-color:#B894F7;
  }
  .btn.now {
    background-color: #652CC4;
  }
  .btn-none {
    background-color: #cccccc;
  }
}
</style>
