<script>
import { getMyOrderList } from '@/api/order.js'
import OrderListItem from '@/components/OrderListItem.vue'

export default {
  name: 'OrderPage',
  components: {
    OrderListItem
  },
  data () {
    return {
      active: this.$route.query.dataType || 'all',
      list: []
    }
  },
  methods: {
    // 获取订单数据
    async getOrderList () {
      const { data: { list } } = await getMyOrderList(this.active)
      this.list = list.data
      // console.log(list.data)
    }

  },
  watch: {
    active: {
      immediate: true,
      handler () {
        this.getOrderList()
      }
    }
  }
}
</script>

<template>
  <div class="order">
    <van-nav-bar title="我的订单" left-arrow @click-left="$router.go(-1)" fixed/>
    <van-tabs v-model="active" calss="tabs" sticky>
      <van-tab name="all" title="全部"></van-tab>
      <van-tab name="payment" title="待支付"></van-tab>
      <van-tab name="delivery" title="待发货"></van-tab>
      <van-tab name="received" title="待收货"></van-tab>
      <van-tab name="comment" title="待评价"></van-tab>
    </van-tabs>

    <OrderListItem v-for="i in list" :key="i.order_id" :item="i"></OrderListItem>

    <!-- 空状态 -->
    <van-empty v-if="list.length <= 0"
    class="custom-image"
    image="https://img01.yzcdn.cn/vant/custom-empty-image.png"
    description="这里空空如也"
    />
  </div>
</template>

<style lang="less" scoped>
.order {
  background-color: #fafafa;
}
.van-tabs {
  margin-top: 50px;
  position: sticky;
  top: 0;
}
</style>
