import { getLocaUserInfo, setgetLocaUserInfo, removegetLocaUserInfo } from '@/utils/store.js'
export default {
  namespaced: true,
  state () {
    return {
      userInfo: getLocaUserInfo()
    }
  },
  mutations: {
    setUserInfo (state, obj) {
      setgetLocaUserInfo(obj)
    }
  },
  actions: {
    logout (context) {
      removegetLocaUserInfo()
      context.commit('cart/serCartList', [], { root: true })
    }
  },
  getters: {}
}
