import { Dialog } from 'vant'

export default {
  methods: {
    loginConfirm () {
      if (!this.$store.getters.token) {
        Dialog.confirm({
          title: '温馨提示',
          message: '您当前还没有登录哟，登陆完再回来吧',
          confirmButtonColor: '#7232DD',
          confirmButtonText: '去登录',
          cancelButtonText: '再逛逛'
        })
          .then(() => {
            this.$router.replace({
              path: '/login',
              query: { backUrl: this.$route.fullPath }
            })
          })
          .catch(() => {})
        return true
      }
      return false
    }
  }
}
