<script>
import { getAddressList, serDefaultAddress, addressDefaultId } from '@/api/address.js'
export default {
  data () {
    return {
      list: [],
      chosenAddressId: ''
    }
  },
  methods: {
    // 新增
    onAdd () {
      this.$router.push('/create')
    },
    // 编辑
    onEdit (e) {
      console.log(e)
    },
    // 切换
    async onSelect (e) {
      console.log(e)
      this.chosenAddressId = e.id
      // const chosenAddressId = this.chosenAddressId.toString()
      await serDefaultAddress(e.id)
      // .then((res) => { console.log(res) })
    },
    // 获取
    async getAddressList () {
      const { data: { list } } = await getAddressList()
      this.list = list
      // console.log(list)
    }
  },
  computed: {
    addresslist () {
      const addresslist = []
      this.list.forEach(i => {
        addresslist.push({
          id: i.address_id,
          name: i.name,
          tel: i.phone,
          address: i.region.province + i.region.city + i.region.region + i.detail
        })
      })

      return addresslist
    }
  },
  async created () {
    const { data: { defaultId } } = await addressDefaultId()
    this.chosenAddressId = defaultId
    // console.log(defaultId)

    this.getAddressList()
  }
}
</script>

<template>
  <div class="address-container">
    <van-nav-bar title="收货地址" left-arrow @click-left="$router.go(-1)" fixed/>

    <div class="address-list">

    </div>
    <van-address-list
    v-model="chosenAddressId"
    :list="addresslist"
    default-tag-text="默认"
    @add="onAdd"
    @edit="onEdit"
    @select="onSelect"
    />
  </div>
</template>

<style lang="less" scoped>
  .address-container{
    width:100%;
    height:auto;
    background-color: #f9f9f9;
  }
  .address-list{
    margin-top: 45px;
  }
</style>
