<!-- eslint-disable vue/multi-word-component-names -->
<script>
import GoodsItem from '@/components/GoodsItem.vue'
import { getProList } from '@/api/produce.js'
export default {
  name: 'SearchIndex',
  data () {
    return {
      sortType: 'all',
      proList: []
    }
  },
  computed: {
    querySearch () {
      return this.$route.query.wd
    }
  },
  methods: {
    changeType (type) {
      this.sortType = type
      this.getProduceList()
    },
    async getProduceList () {
      const { data: { list } } = await getProList({
        sortType: this.sortType,
        categoryId: this.$route.query.categoryId,
        goodsName: this.querySearch
      })
      this.proList = list.data
    }
  },
  created () {
    this.getProduceList()
  },
  components: {
    GoodsItem
  }
}
</script>

<template>
  <div class="search">
    <van-nav-bar fixed title="商品列表" left-arrow @click-left="$router.go(-1)" />

    <van-search
      readonly
      shape="round"
      background="#ffffff"
      :value="querySearch || '搜索商品'"
      show-action
      @click="$router.push('/search')"
    >
      <template #action>
        <van-icon class="tool" name="apps-o" />
      </template>
    </van-search>

    <!-- 排序选项按钮 -->
    <div class="sort-btns">
      <div class="sort-item" @click="changeType('all')">综合</div>
      <div class="sort-item" @click="changeType('sales')">销量</div>
      <div class="sort-item" @click="changeType('price')">价格 </div>
    </div>

    <div class="goods-list" v-if="proList.length > 0">
      <GoodsItem v-for="i in proList" :key="i.goods_id" :item="i"></GoodsItem>
    </div>

    <!-- 空状态 -->
    <van-empty v-if="proList.length <= 0" image="search" description="搜索内容空空如也" />
  </div>
</template>

<style lang="less" scoped>
.search {
  padding-top: 46px;
  ::v-deep .van-icon-arrow-left {
    color: #333;
  }
  .tool {
    font-size: 24px;
    height: 40px;
    line-height: 40px;
  }

  .sort-btns {
    display: flex;
    height: 36px;
    line-height: 36px;
    .sort-item {
      text-align: center;
      flex: 1;
      font-size: 16px;
    }
  }
}

// 商品样式
.goods-list {
  background-color: #f6f6f6;
}
</style>
