<!-- eslint-disable vue/multi-word-component-names -->
<script>
import { getOrderDetail, orderCancel } from '@/api/order.js'
import { Toast, Dialog } from 'vant'
export default {
  data () {
    return {
      orderData: {},
      addressData: {}
    }
  },
  methods: {
    async getOrderDetail () {
      const { data: { order } } = await getOrderDetail(this.orderId)
      // console.log(order)
      this.orderData = order
      this.addressData = order.address
    },
    async cancelOrder () {
      Dialog.confirm({
        title: '温馨提示',
        message: '您确定需要取消该订单么？'
      }).then(async () => {
        const { data: res } = await orderCancel(this.orderId)
        this.getOrderDetail()
      })
        .catch(() => {})
    }
  },
  computed: {
    orderId () {
      return this.$route.query.orderId
    },
    longAddress () {
      const region = this.addressData.region
      return region.province + region.city + region.region + this.addressData.detail
    }
  },
  async created () {
    this.getOrderDetail()
  }
}
</script>

<template>
  <div class="detail-container">
    <div class="nav-bar">
      <span @click="$router.go(-1)"><van-icon name="arrow-left" /></span>
      <p >订单详细</p>
    </div>
    <div class="header-box">
      <img src="http://cba.itlike.com/public/mweb/static/order/status/wait_deliver.png" alt="">
      <h1>{{ orderData.state_text }}</h1>
      <div class="address-box">
        <div class="userInfo">
          <span>{{ addressData.name }}</span>
          <span>{{ addressData.phone }}</span>
        </div>
        <span class="address">{{longAddress}}</span>
      </div>

      <div class="order-goods">
        <div class="list" v-for="g in orderData.goods" :key="g.goods_id" @click="$router.push(`/prodetail/${g.goods_id}`)">
          <div class="list-item">
            <div class="goods-img">
              <img :src="g.goods_image" alt="">
            </div>
            <div class="goods-content text-ellipsis-2">
              {{g.goods_name}}
            </div>
            <div class="goods-trade">
              <p>¥ {{ g.goods_price }}</p>
              <p>x {{ g.total_num }}</p>
            </div>
          </div>
        </div>
      </div>

        <div class="order-detail">
          <div class="detail-items">
            <span>订单编号</span>
            <p>{{ orderData.order_no }}</p>
          </div>
          <div class="detail-items">
            <span>下单时间</span>
            <p>{{ orderData.create_time }}</p>
          </div>
          <div class="detail-items">
            <span>买家留言</span>
            <p>{{ orderData.buyer_remark || '---' }}</p>
          </div>
        </div>

        <div class="detail-price">
          <div class="detail-items">
            <span>订单金额</span>
            <p>￥{{ orderData.pay_price }}</p>
          </div>
          <div class="detail-items">
            <span>运费</span>
            <p>+ ￥0.00</p>
          </div>
          <div class="detail-items-price">
            <span>实付款</span>
            <p>￥{{ orderData.total_price }}</p>
          </div>
        </div>

        <div class="footer"></div>

        <div class="cancel-box">
          <button @click="cancelOrder" v-if="orderData.state_text !== '待取消'">取消订单</button>
          <p v-if="orderData.state_text === '待取消'">取消申请中</p>
        </div>
    </div>

  </div>
</template>

