// 地址
import $axios from '@/utils/axios'

// 获取收货地址接口
export const getAddressList = () => $axios.get('/address/list')

// 设置默认地址接口
export const serDefaultAddress = (addressId) => $axios.post('/address/setDefault', {
  addressId
})

// 根据默认地址ID获取收货地址
export const getdefaulAddress = (addressId) => $axios.get('/address/detail', {
  params: {
    addressId
  }
})

// 请求默认地址ID接口
export const addressDefaultId = () => $axios.get('/address/defaultId')

// 获取省市区地址接口
export const getRegionTree = () => $axios.get('/region/tree')

// 添加收货地址接口
export const addressAdd = (name, phone, region) => $axios.post('/address/add', {
  form: {
    name,
    phone,
    region
  }
})
