import Vue from 'vue'
import VueRouter from 'vue-router'
import store from '@/store'

import Login from '@/views/login/index.vue'
import Search from '@/views/search/index.vue'
import SearchList from '@/views/search/searchlist.vue'
import Prodetail from '@/views/prodetail/index.vue'
import Pay from '@/views/pay/index.vue'
import MyOrder from '@/views/myorder/index.vue'
import Detail from '@/views/myorder/detail.vue'
import Address from '@/views/address/index.vue'
import NewAddress from '@/views/address/newAddress.vue'
import UserInfo from '@/views/userinfo/index.vue'

const Layout = () => import('@/views/layout/index.vue')
const Home = () => import('@/views/layout/home.vue')
const Category = () => import('@/views/layout/category.vue')
const Cart = () => import('@/views/layout/cart.vue')
const User = () => import('@/views//layout/user.vue')

Vue.use(VueRouter)

const router = new VueRouter({
  routes: [
    { path: '/login', component: Login },
    {
      path: '/',
      component: Layout,
      redirect: '/home',
      children: [
        { path: '/home', component: Home },
        { path: '/category', component: Category },
        { path: '/cart', component: Cart },
        { path: '/user', component: User }
      ]
    },
    { path: '/search', component: Search },
    { path: '/searchlist', component: SearchList },
    { path: '/prodetail/:id', component: Prodetail },
    { path: '/pay', component: Pay },
    { path: '/myorder', component: MyOrder },
    { path: '/detail', component: Detail },
    { path: '/address', component: Address },
    { path: '/create', component: NewAddress },
    { path: '/userinfo', component: UserInfo }
  ]
})

const authUrls = ['/pay', '/myorder']
router.beforeEach((to, form, next) => {
  if (!authUrls.includes(to.path)) {
    next()
    return true
  }

  // 能执行到这 声明是需要权限的页面
  const token = store.getters.token
  if (!token) {
    next('/login')
  } else {
    next()
  }
})

export default router
