import { getCartList, changeCount, delSelect } from '@/api/cart'
import { Toast } from 'vant'

export default {
  namespaced: true,
  state () {
    return {
      cartList: []
    }
  },
  mutations: {
    // 修改 是否选中
    toggleCheck (state, goodsId) {
      const goods = state.cartList.find(i => i.goods_id === goodsId)
      goods.isChecked = !goods.isChecked
    },

    // 修改 是否全选
    toggleAllCheck (state, flag) {
      state.cartList.forEach(i => {
        i.isChecked = flag
      })
    },

    // 重置购物车
    serCartList (state, arr) {
      state.cartList = arr
    }
  },
  actions: {
    async getCartDataAction (context) {
      const { data: { list } } = await getCartList()
      list.forEach(i => {
        i.isChecked = true
      })
      context.state.cartList = list.reverse() // 倒序
    },

    async changeCountAction (context, Obj) {
      const { value, goodsId, skuId } = Obj
      await changeCount(goodsId, value, skuId)
      const obj = context.state.cartList.find(i => i.goods_id === goodsId)
      obj.goods_num = value
    },

    async delSelect (context) {
      const selChecked = context.getters.selChecked // 选中的项
      const cartIds = selChecked.map(i => i.id)
      if (cartIds.length <= 0) return true
      await delSelect(cartIds)
      Toast('删除成功')
      context.dispatch('getCartDataAction')
      return false
    }
  },
  getters: {
    cartTotal (state) {
      return state.cartList.reduce((sum, i, index) => sum + i.goods_num, 0)
    },

    selChecked (state) {
      return state.cartList.filter(i => i.isChecked)
    },

    selCount (state, getters) {
      return getters.selChecked.reduce((sum, i, index) => sum + i.goods_num, 0)
    },

    selPrice (state, getters) {
      return getters.selChecked.reduce((sum, i) => {
        return sum + i.goods_num * i.goods.goods_price_min
      }, 0).toFixed(2)
    },

    isAllChecked (state) {
      return state.cartList.every(i => i.isChecked)
    }
  }
}
