<script>
import { loginGetPicCode, loginGetMsgCode, useLogin } from '@/api/login.js'
import { Toast } from 'vant'
export default {
  data () {
    return {
      name: 'LoginIndex',
      isCountDown: '获取验证码',
      isdisabled: false,
      timer: '', // 定时器

      picCode: '', // 用户输入的图形验证码
      picKey: '', // 将来请求传递的图形验证码唯一标识
      picUrl: '', // 图形验证码图片地址
      mobile: '', // 手机号
      msgCode: '' // 短信验证码
    }
  },

  methods: {
    onClickLeft () {
      this.$router.go(-1)
    },
    // 校验 手机号 与 输入的图形验证码方法
    validFn () {
      if (!/^1[3-9]\d{9}$/.test(this.mobile)) {
        Toast('请输入正确的手机号')
        return false
      }
      if (!/^\w{4}$/.test(this.picCode)) {
        this.getPicCode()
        Toast('请输入正确的图形验证码')
        return false
      }
      return true
    },
    // 获取图形验证码方法
    async getPicCode () {
      const { data: res } = await loginGetPicCode()
      this.picUrl = res.base64
      this.picKey = res.key
    },
    // 获取短信验证码方法
    async onCountDown () {
      if (!this.validFn()) return

      // 调用获取短信验证码接口
      const res = await loginGetMsgCode(this.picCode, this.picKey, this.mobile)

      if (res.status !== 200) {
        Toast('输入的验证码错误')
        this.getPicCode()
        return
      } else {
        Toast.success('获取验证码成功')
      }

      // 获取倒计时
      let i = 60
      this.timer = setInterval(() => {
        i--
        this.isdisabled = true
        this.isCountDown = i + '秒后重新获取'
        if (i <= 0) {
          clearInterval(this.timer)
          this.isdisabled = false
          this.isCountDown = '重新获取'
        }
      }, 1000)
    },

    // 登录
    async onLogin () {
      if (!this.validFn()) return true

      // 短信验证码校验
      if (!/^\d{6}$/.test(this.msgCode)) {
        Toast('请输入验证码')
        return true
      }

      // 调用登录接口
      const res = await useLogin(this.mobile, this.msgCode)
      Toast.success(res.message)
      // 储存用户信息到本地
      this.$store.commit('user/setUserInfo', res.data)
      // 跳转页面
      const url = this.$route.query.backUrl || '/'
      this.$router.replace(url)
    }
  },

  created () {
    // 调用获取图形验证码接口
    this.getPicCode()
  }
}
</script>

<template>
  <div>
    <van-nav-bar
    title="用户登录"
    left-arrow
    @click-left="onClickLeft"
    />

    <div class="title-card">
      <p>手机号码登录</p>
      <span>未注册的手机号码登录后自动注册</span>
    </div>

    <div class="input-card">
      <div class="item">
        <input v-model="mobile" type="text" placeholder="请输入手机号码">
      </div>
      <div class="item">
        <input v-model="picCode" type="text" placeholder="请输入图形验证码">
        <img @click="getPicCode" :src="picUrl" v-if="picUrl" >
      </div>
      <div class="item">
        <input v-model.number="msgCode" type="text" placeholder="请输入短信验证码">
        <button @click="onCountDown" :disabled="isdisabled">{{ isCountDown }}</button>
      </div>

      <button @click="onLogin" class="login">登录</button>
    </div>
  </div>
</template>

<style lang="less" scoped>
.title-card{
  width: 100%;
  height: auto;
  margin-top: 60px;
  p{
    font-size: 29px;
    padding: 10px 0 10px 30px;
  }
  span{
    padding: 0 0 0px 30px;
    color: #c6c6c6;
    font-size: 15px;
  }
}

.input-card{
  width: 100%;
  height: auto;
  margin-top: 30px;
  .item{
    padding: 20px 0 0 30px;
    position: relative;
    input{
      width: 90%;
      height: 40px;
      padding-left: 5px;
      outline: none;
      border: none;
      border-bottom: 1px solid #e9e9e9;
    }
    img{
      width: 100px;
      height: 40px;
      position: absolute;
      right: 36px;
      bottom: 5px;
    }
    button{
      position: absolute;
      font-size: 14px;
      color: #7b58c2;
      left: 245px;
      bottom: 15px;
      border: none;
      background-color: white;
    }
  }

  .login{
    width: 83%;
    height: 37px;
    margin: 50px 0 0 30px;
    border: none;
    border-radius: 5px;
    color: white;
    background-color: #7b58c2;
  }
  .login:active{
      background-color: #6c3bcd;
    }
}
</style>
