// 购物车
import $axios from '@/utils/axios'

// 加入购物车接口
export const useAddCart = (goodsId, goodsNum, goodsSkuId) => $axios.post('/cart/add', {
  goodsId,
  goodsNum,
  goodsSkuId
})

// 获取购物车列表接口
export const getCartList = () => $axios.get('/cart/list')

// 更新购物车数量接口
export const changeCount = (goodsId, goodsNum, goodsSkuId) => $axios.post('/cart/update', {
  goodsId,
  goodsNum,
  goodsSkuId
})

// 删除购物车商品接口
export const delSelect = (cartIds) => $axios.post('/cart/clear', {
  cartIds
})
