<script>
import { uploadAvatar } from '@/api/avatar.js'
export default {
  data () {
    return {
      uploader: [{ url: 'https://img01.yzcdn.cn/vant/leaf.jpg' }],
      username: '',
      phone: ''
    }
  },
  methods: {
    // 上传头像
    afterRead (file) {
      console.log(file)
      this.uploader = []
      this.uploader.push(file)
    },
    // 更新头像
    async updateAvtar () {
      const { data: { fileInfo } } = await uploadAvatar(this.uploader[0].content)
      console.log(fileInfo)
    },
    onFailed () {}
  }
}
</script>

<template>
  <div>
    <van-nav-bar title="用户信息" left-arrow @click-left="$router.go(-1)" fixed/>
    <div class="uploader-box">
      <van-field name="uploader" label="文件上传">
        <template #input>
          <van-uploader v-model="uploader" :max-count="1"/>
        <van-uploader :after-read="afterRead" >
          <van-button icon="plus" type="primary">上传文件</van-button>
        </van-uploader>

        <van-button round type="info" @click="updateAvtar">更新头像</van-button>
        </template>
      </van-field>

      <van-form validate-first @failed="onFailed">
        <!-- 通过 pattern 进行正则校验 -->
        <van-field
          v-model="username"
          name="用户名"
          label="用户名"
          placeholder="输入用户名"
          :rules="[{ username, message: '请输入正确内容' }]"
        />
        <!-- 通过 validator 进行函数校验 -->
        <van-field
          v-model="phone"
          name="手机号"
          label="手机号"
          placeholder="输入手机号"
          :rules="[{ phone, message: '请输入正确内容' }]"
        />
        <div style="margin: 16px;">
          <van-button round block type="info" native-type="submit">提交</van-button>
        </div>
      </van-form>
    </div>
  </div>
</template>

<style lang="less" scoped>
.uploader-box{
  margin-top: 50px;
}
</style>
