// 登录
import $axios from '@/utils/axios'

// 获取图形验证码接口
export const loginGetPicCode = () => $axios.get('/captcha/image')

// 获取短信验证码接口
export const loginGetMsgCode = (captchaCode, captchaKey, mobile) => $axios.post('/captcha/sendSmsCaptcha', {
  form: {
    captchaCode,
    captchaKey,
    mobile
  }
})

// 登录接口
export const useLogin = (mobile, smsCode) => $axios.post('/passport/login', {
  form: {
    isParty: false,
    mobile,
    partyData: {},
    smsCode
  }
})
