// 订单
import $axios from '@/utils/axios'

// 订单详细接口
export const checOrder = (mode, obj) => $axios.get('/checkout/order', {
  params: {
    mode,
    ...obj,
    delivery: 10,
    couponId: 0,
    isUsePoints: 0
  }
})

// 提交订单接口
export const checSubmit = (mode, params) => $axios.post('/checkout/submit', {
  mode,
  ...params,
  delivery: 10, // 物流方式  配送方式 (10快递配送 20门店自提)
  couponId: 0, // 优惠券 id
  payType: 10, // 余额支付
  isUsePoints: 0 // 是否使用积分
})

// 我的订单
export const getMyOrderList = (dataType) => $axios.get('/order/list', {
  params: {
    dataType,
    page: 1
  }
})

// 订单详细接口
export const getOrderDetail = (orderId) => $axios.get('/order/detail', {
  params: {
    orderId
  }
})

// 商品状态数量接口
export const orderTodoCounts = () => $axios.get('/order/todoCounts')

// 取消订单
export const orderCancel = (orderId) => $axios.post('/order/cancel', {
  orderId
})
